
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.conversation-container {
  background-color: #fff;
  position: relative;
}
.conversation-container.collapsed {
  overflow: hidden;
}
.conversation-container h5 {
  margin: 0;
  padding: 0;
  color: #444;
  height: 60px;
  line-height: 60px;
  width: 100%;
}
.conversation-container #activity-visualisation {
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 0;
}
.conversation-container #activity-visualisation svg {
  overflow: visible;
}
.conversation-container #activity-visualisation svg path {
  fill: #29b474;
}
@media (max-width: 600px) {
.conversation-container #activity-visualisation {
    display: none;
}
}
.conversation-container .inner-wrapper {
  background-color: #fff;
  background: url("../assets/images/line.png");
  background-repeat: repeat;
  background-position: center -1px;
  overflow: hidden;
}
.conversation-container .inner-wrapper .subtitle-wrapper,
.conversation-container .inner-wrapper .message-wrapper {
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
  width: 50%;
}
.conversation-container .inner-wrapper .subtitle-wrapper.message-wrapper,
.conversation-container .inner-wrapper .message-wrapper.message-wrapper {
  -webkit-transform: translate(100%, -50%);
  transform: translate(100%, -50%);
}
@media (max-width: 600px) {
.conversation-container .inner-wrapper .subtitle-wrapper,
  .conversation-container .inner-wrapper .message-wrapper {
    width: 100%;
}
.conversation-container .inner-wrapper .subtitle-wrapper.subtitle-wrapper,
  .conversation-container .inner-wrapper .message-wrapper.subtitle-wrapper {
    display: block;
}
.conversation-container .inner-wrapper .subtitle-wrapper.message-wrapper,
  .conversation-container .inner-wrapper .message-wrapper.message-wrapper {
    display: none;
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper {
    width: 100%;
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper.subtitle-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper.subtitle-wrapper {
    display: none;
}
.conversation-container .inner-wrapper.message-priority .subtitle-wrapper.message-wrapper,
  .conversation-container .inner-wrapper.message-priority .message-wrapper.message-wrapper {
    display: block;
    -webkit-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
}
}
#view-toggle {
  transition: all 0.3s ease;
  border-radius: 4px;
  background-color: #29b474;
  color: #fff;
  display: none;
  height: 100px;
  width: 60px;
  position: fixed;
  top: 80px;
  right: -10px;
  z-index: 55;
}
#view-toggle.peeking {
  z-index: 57;
}
@media (max-width: 600px) {
#view-toggle {
    display: block;
}
}
#view-toggle > .fa-icon {
  display: block;
  height: 50px;
  padding: 0 15px;
  width: 20px;
}
#view-toggle > .fa-icon:first-child {
  opacity: 0.5;
}
#view-toggle > .fa-icon:last-child {
  border-top: rgba(0,0,0,0.1) 1px solid;
  opacity: 1;
}
#view-toggle.message-priority > .fa-icon:first-child {
  opacity: 1;
}
#view-toggle.message-priority > .fa-icon:last-child {
  opacity: 0.5;
}


