
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.single-message-wrapper {
  position: relative;
}
.single-message-wrapper .message {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  max-height: 128px;
  margin: 10px 15px;
  padding: 0 10px 40px 10px;
  padding-left: 50px;
  position: relative;
}
.single-message-wrapper .message img.profile-image {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  position: absolute;
  top: 0px;
  left: 0px;
}
.single-message-wrapper .message p.message-content {
  margin: 0;
  padding: 0;
  color: #444;
  max-height: 70px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  word-wrap: break-word;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.single-message-wrapper .message p.message-content a,
.single-message-wrapper .message p.message-content a:active {
  color: #444;
}
.single-message-wrapper .message a.author-label {
  color: #444;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.9em;
}
.single-message-wrapper .message a.author-label .fa-icon {
  color: #29b474;
  height: 12px;
  margin: 0 10px;
}
.single-message-wrapper .message .message--footer {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 36px;
  left: 42px;
  top: auto;
  overflow: hidden;
  position: absolute;
}
.single-message-wrapper .message .message--footer ul.tweet-actions {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: inherit;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  transition: all 0.3s ease;
  color: #ccc;
  float: left;
  font-size: 1em;
  line-height: 36px;
  margin: 0 10px;
  max-width: 15%;
  text-decoration: none;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li:hover {
  cursor: pointer;
  color: #29b474;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li.like-action:hover,
.single-message-wrapper .message .message--footer ul.tweet-actions li.like-action.active {
  color: #f00;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li.retweet-action:hover,
.single-message-wrapper .message .message--footer ul.tweet-actions li.retweet-action.active {
  color: #4099ff;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li.reply-action:hover,
.single-message-wrapper .message .message--footer ul.tweet-actions li.reply-action.active {
  color: #29b474;
}
.single-message-wrapper .message .message--footer ul.tweet-actions li.message-timestamp {
  color: #ccc;
  font-size: 0.9em;
  float: right;
  max-width: 55%;
}
.single-message-wrapper .replies-wrapper {
  margin-left: 20px;
}


