
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
  display: inline-block;
  font-family: inherit;
  font-size: 100%;
  outline: 0;
  padding: 0.5em 1em;
  text-align: center;
  text-decoration: none;
}
.pure-button:hover {
  background-color: #29b474;
  color: #fff;
  cursor: pointer;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-info {
  background-color: #328dd4;
  border-color: #328dd4;
  color: #fff;
}
.pure-button.pure-button-info:hover {
  background-color: #297fc3;
}
.pure-button.pure-button-white {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
}
.pure-button.pure-button-white:hover {
  background-color: rgba(0,0,0,0.1);
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-continue {
  border-radius: 25px;
  background-color: #29b474;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-continue:hover {
  background-color: #25a268;
}
.pure-button.pure-button-youtube {
  border-radius: 25px;
  background-color: #c4302b;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-youtube:hover {
  background-color: #b02b27;
}
.pure-button.pure-button-soundcloud {
  border-radius: 25px;
  background-color: #f70;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-soundcloud:hover {
  background-color: #e66b00;
}
.pure-button.pure-button-twitter {
  border-radius: 25px;
  background-color: #4099ff;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-twitter:hover {
  background-color: #2088ff;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
#join-banner.course-content {
  background-color: #fff;
}
@media (min-width: 800px) {
#join-banner.course-content {
    border-radius: 4px;
    box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
    -moz-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
    -webkit-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
}
}
@media (max-width: 800px) {
#join-banner.course-content {
    border-radius: 0;
    margin: 20px 0 0 0;
}
}
#join-banner.course-content .course-content--header {
  padding: 10px 20px 30px 20px;
  text-align: center;
}
#join-banner.course-content .course-content--header h1.content-title {
  color: #444;
  margin: 20px auto;
}
#join-banner.course-content .course-content--header .auth-button-wrapper {
  margin: 10px auto 0 auto;
  text-align: center;
}


