.pure-button {
  transition: all 0.3s ease;
  border-radius: 4px;
  background-color: transparent;
  border: #29b474 1px solid;
  color: #29b474;
  display: inline-block;
  font-family: inherit;
  font-size: 100%;
  outline: 0;
  padding: 0.5em 1em;
  text-align: center;
  text-decoration: none;
}
.pure-button:hover {
  background-color: #29b474;
  color: #fff;
  cursor: pointer;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.pure-button.pure-button-primary {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-primary:hover {
  background-color: #25a268;
}
.pure-button.pure-button-info {
  background-color: #328dd4;
  border-color: #328dd4;
  color: #fff;
}
.pure-button.pure-button-info:hover {
  background-color: #297fc3;
}
.pure-button.pure-button-white {
  background-color: transparent;
  border-color: #fff;
  color: #fff;
}
.pure-button.pure-button-white:hover {
  background-color: rgba(0,0,0,0.1);
}
.pure-button.pure-button-success {
  background-color: #29b474;
  color: #fff;
}
.pure-button.pure-button-success:hover {
  background-color: #25a268;
}
.pure-button.pure-button-continue {
  border-radius: 25px;
  background-color: #29b474;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-continue:hover {
  background-color: #25a268;
}
.pure-button.pure-button-youtube {
  border-radius: 25px;
  background-color: #c4302b;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-youtube:hover {
  background-color: #b02b27;
}
.pure-button.pure-button-soundcloud {
  border-radius: 25px;
  background-color: #f70;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-soundcloud:hover {
  background-color: #e66b00;
}
.pure-button.pure-button-twitter {
  border-radius: 25px;
  background-color: #4099ff;
  border: none;
  color: #fff;
  line-height: 50px;
  padding: 0 30px;
}
.pure-button.pure-button-twitter:hover {
  background-color: #2088ff;
}
.pure-button.pure-button-subtle {
  background-color: transparent;
  border-color: #ccc;
  color: #666;
}
.pure-button.pure-button-subtle:hover {
  background-color: #e1e1e1;
  color: #444;
}
.pure-button.pure-button-text {
  border-color: transparent;
}
.pure-button.pure-button-homework {
  background-color: transparent;
  border-color: #fd3c51;
  color: #fd3c51;
}
.pure-button.pure-button-homework:hover {
  background-color: #fd3c51;
  color: #fff;
}
.four-corners-link {
  border-radius: 6px;
  background-color: #f9f9f9;
  border: #e9e9e9 1px solid;
  overflow: hidden;
  margin: 20px 0;
}
.four-corners-link .four-corners-link--body {
  border-bottom: #e9e9e9 1px solid;
  height: 32px;
  padding: 10px;
}
.four-corners-link .four-corners-link--body img {
  height: 32px;
}
.four-corners-link .four-corners-link--footer {
  padding: 15px;
}
.four-corners-link .four-corners-link--footer h1,
.four-corners-link .four-corners-link--footer p {
  color: #000;
}
.four-corners-link .four-corners-link--footer p {
  margin: 0;
  padding: 0;
  padding: 0px 0 20px 0;
}


