
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.course-content-wrapper {
  margin-bottom: 10px;
}
.course-content-wrapper:last-child {
  margin-bottom: 0;
}
.course-content-wrapper .course-content-group:first-child .course-content {
  margin-top: 20px;
}
.course-content-wrapper .course-content-group .course-content {
  background-color: #fff;
  margin: 30px 0 0 0;
  min-height: 100px;
  padding: 0;
  position: relative;
}
@media (min-width: 800px) {
.course-content-wrapper .course-content-group .course-content {
    box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
    -moz-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
    -webkit-box-shadow: 0 0 10px 5px rgba(0,0,0,0.01);
    border-radius: 4px;
}
}
@media (max-width: 800px) {
.course-content-wrapper .course-content-group .course-content {
    margin: 30px 0 0 0;
}
}
.course-content-wrapper .course-content-group .course-content h5.content-subheader {
  margin: 0;
  padding: 0;
  color: #444;
  font-weight: 300;
}
.course-content-wrapper .course-content-group .course-content p.content-description {
  margin: 0;
  padding: 0;
  color: #666;
  font-size: 1em;
}
.course-content-wrapper .course-content-group .course-content a {
  text-decoration: none;
}
.course-content-wrapper .course-content-group .course-content .course-content--header {
  background-color: transparent;
  overflow: hidden;
  padding: 20px 20px 10px 20px;
}
.course-content-wrapper .course-content-group .course-content .course-content--header h1.content-title {
  margin: 0;
  padding: 0;
  color: #444;
  font-size: 1.3em;
  font-weight: bold;
}
.course-content-wrapper .course-content-group .course-content .course-content--header.block {
  background-color: #29b474;
  padding: 30px;
  text-align: center;
}
.course-content-wrapper .course-content-group .course-content .course-content--header.block h1.content-title {
  color: #fff;
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 10px;
}
.course-content-wrapper .course-content-group .course-content .course-content--header.block p.content-description {
  color: #fff;
  margin: 0 auto;
}
.course-content-wrapper .course-content-group .course-content .course-content--body {
  overflow: hidden;
  padding: 0px 20px 10px 20px;
}
.course-content-wrapper .course-content-group .course-content .course-content--body h1,
.course-content-wrapper .course-content-group .course-content .course-content--body h2,
.course-content-wrapper .course-content-group .course-content .course-content--body h3,
.course-content-wrapper .course-content-group .course-content .course-content--body h4,
.course-content-wrapper .course-content-group .course-content .course-content--body h5 {
  font-weight: 300;
}
.course-content-wrapper .course-content-group .course-content .course-content--body h2 {
  color: #444;
  font-size: 1.3em;
  font-weight: bold;
}
.course-content-wrapper .course-content-group .course-content .course-content--footer {
  padding: 20px;
}
.course-content-wrapper .course-content-group.course-content-group--question {
  border-radius: 6px;
  background-color: rgba(255,255,255,0.15);
  padding: 15px;
  margin-top: 10px;
}
.course-content-wrapper .course-content-group.course-content-group--question .course-content {
  margin: 0;
}


