
.hidden,
[v-cloak] {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.background-white {
  background-color: #fff !important;
}
.main-container {
  border-radius: 4px;
  position: relative;
}
@media (max-width: 800px) {
.main-container {
    border-radius: 0;
}
}
.main-container.main-container-padded {
  padding: 20px;
}
.content-block {
  border-radius: 4px;
  padding: 20px;
  margin: 20px 0 0 0;
}
@media (max-width: 800px) {
.content-block {
    border-radius: 0;
}
}
.content-block.white-block {
  background-color: #fff;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.media-carousel-wrapper {
  background-color: #161616;
}
.media-carousel-wrapper .slick-slider button.slick-arrow {
  transition: all 0.3s ease;
  border-radius: 4px;
  border: none;
  background: rgba(22,22,22,0.5);
  margin-top: -46px;
  outline: 0;
  padding: 30px 15px;
  position: absolute;
  top: 50%;
  z-index: 1;
}
.media-carousel-wrapper .slick-slider button.slick-arrow:hover {
  cursor: pointer;
  background: rgba(22,22,22,0.8);
}
.media-carousel-wrapper .slick-slider button.slick-arrow .fa-icon {
  height: 30px;
  color: #fff;
}
.media-carousel-wrapper .slick-slider button.slick-arrow.slick-prev {
  left: 8px;
}
.media-carousel-wrapper .slick-slider button.slick-arrow.slick-next {
  right: 8px;
}
.media-carousel-wrapper .slick-track {
  z-index: 1;
  position: relative;
}
.media-carousel-wrapper .slick-track .slick-slide {
  transition: all 0.3s ease;
  opacity: 0.3;
  outline: 0;
}
.media-carousel-wrapper .slick-track .slick-slide img {
  height: 204px;
  max-height: 204px;
  max-width: 100%;
  margin: 0 10px;
}
.media-carousel-wrapper .slick-track .slick-slide:hover {
  cursor: pointer;
}
.media-carousel-wrapper .slick-track .slick-slide.slick-current {
  opacity: 1;
}
.media-carousel-wrapper .slick-track {
  z-index: 1;
  position: relative;
}
.media-carousel-wrapper .slick-track .slick-slide {
  opacity: 0.5;
  outline: 0;
}
.media-carousel-wrapper .slick-track .slick-slide img {
  height: 204px;
  max-height: 204px;
  max-width: 100%;
  margin: 0 10px;
}
.media-carousel-wrapper .slick-track .slick-slide:hover {
  cursor: pointer;
}
.media-carousel-wrapper .slick-track .slick-slide.slick-current {
  opacity: 1;
}
.media-carousel-wrapper .slick-track {
  z-index: 1;
  position: relative;
}
.media-carousel-wrapper .slick-track .slick-slide {
  opacity: 0.5;
  outline: 0;
}
.media-carousel-wrapper .slick-track .slick-slide img {
  height: 300px;
  max-height: 300px;
  max-width: 100%;
  margin: 0 10px;
}
.media-carousel-wrapper .slick-track .slick-slide:hover {
  cursor: pointer;
}
.media-carousel-wrapper .slick-track .slick-slide.slick-current {
  opacity: 1;
}


